import React, { useState} from 'react'
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Grid from '@material-ui/core/Grid';
import CircularProgress from '@material-ui/core/CircularProgress';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import {connect} from 'react-redux'
import { doFetchAut } from '../../actions/autenticacao';


const useStyles = makeStyles(theme => ({
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(3),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
    buttonProgress: {
        color: theme.palette.secondary.main,
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    },
    wrapper: {
        margin: theme.spacing(1),
        position: 'relative',
    },
    msgerro: {
        padding: theme.spacing(2),
        backgroundColor: theme.palette.error.main,
    }
}));

function Erro(props) {
    return (
        <Grid item xs={12}>
            <Typography component="h1" variant="h6" color="error">
                {`${props.erro}`}
            </Typography>
        </Grid>
    )
}


const Login = ({autenticacao, onLogin, navegacao}) => {

    const {
        autusuario,
        autservidor
    } = autenticacao


    const {
        isError,
        erro,
        loading
    } = navegacao
    const classes = useStyles();

    const [aut, setAut] = useState(
        {
            autusuario: autusuario,
            autlogado: false,
            autsenha: '',
            autservidor: autservidor 
        }
    )
    
    const atualizaCampos = e => {
        const valor = e.target.id !== 'autlogado' ? e.target.value: e.target.checked;
        setAut({
            ...aut,
            [e.target.id]: valor
        });
    };

    

    return (
        
        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5" color="textSecondary">
                    Login
                </Typography>
                <form className={classes.form}
                    noValidate
                    >

                    <Grid container spacing={2}>

                        <Grid item xs={12}>
                            <TextField
                                autoComplete="autusuario"
                                name="autusuario"
                                variant="outlined"
                                required
                                fullWidth
                                id="autusuario"
                                label="Usuário"
                                autoFocus
                                value={aut.autusuario}
                                onChange={atualizaCampos} />
                        </Grid>

                        <Grid item xs={12}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                name="autsenha"
                                label="Senha"
                                type="password"
                                id="autsenha"
                                autoComplete="current-password"
                                value={aut.autsenha}
                                onChange={atualizaCampos} />
                        </Grid>
                        {"CONFIGURACAO" &&
                            <Grid item xs={12}>
                                <TextField
                                    variant="outlined"
                                    required
                                    fullWidth
                                    id="autservidor"
                                    label="Servidor"
                                    name="autservidor"
                                    autoComplete="autservidor"
                                    value={aut.autservidor}
                                    onChange={atualizaCampos} />
                            </Grid>}
                        <Grid item xs={12}>
                            <FormControlLabel
                                control={<Checkbox
                                    id="autlogado"
                                    value={aut.autlogado}
                                    color="primary"
                                    onChange={atualizaCampos} />}
                                label="Permanecer logado"
                            />
                        </Grid>
                        {isError && <Erro erro={erro}/>}
                    </Grid>
                    <div className={classes.wrapper}>
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            disabled={loading}
                            className={classes.submit}                            
                            onClick={(e)=>{e.preventDefault();onLogin(aut)}}>
                            Entrar</Button>
                        {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                    </div>
                </form>
            </div>
        </Container >

    )
}

const mapStateToProps = (state) => ({
    autenticacao: state.autenticacao,
    navegacao: state.navegacao,
})

const mapDispatchToProps = dispatch => ({
    onLogin : login => dispatch(doFetchAut(login)),
})


export default connect(mapStateToProps,mapDispatchToProps)(Login)