import { PAR_ADD } from "../constantes/actionsTypes";

const INITIAL_STATE = null

const applyAddParametros = (state, action) => {
    if (!action.parametros) {
        return {};
    }
    return action.parametros;
}


function parReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case PAR_ADD: {
            return applyAddParametros(state, action);
        }
        default: return state;
    }
}

export default parReducer;