import axios from 'axios';
import { API_BASE_URL } from '../constantes/apiConst';

const fetchSinc = async (login,ident,metodo) => {
    const response = await axios.get(
        `http://${login.autservidor}${API_BASE_URL}metodo=${metodo}&vendedor=${ident.vd1codig}&filial=${ident.filcodig}`
    );

    return response.data;
};


export {
    fetchSinc,
};