import db from './dbvendas'

const dbAddSincronizacao = async (sincronizacao) => {
    const sinc = sincronizacao ? sincronizacao : {
        sincpar: 'sincultima',
        sincvalor: 0
    };
    
    
    await db.sincronizacao.put(sinc);
    const response = await dbLoadSincronizacao();
    return response;
}

const dbLoadSincronizacao = async () => {
    const sincronizacao = await db.sincronizacao
    .where('sincpar')
    .equals('sincultima')
    .first();
    return sincronizacao;
}

const dbLoadAddSincronizacao = async () => {
    const sincronizacao = await db.sincronizacao
    .where('sincpar')
    .equals('sincultima')
    .first();
    
    if(!sincronizacao){
        return await dbAddSincronizacao(sincronizacao);
    }
    return sincronizacao;
}

export {
    dbAddSincronizacao,
    dbLoadSincronizacao,
    dbLoadAddSincronizacao,
}