import React, { useEffect, Fragment } from 'react';

import { ThemeProvider } from '@material-ui/styles';
import theme from '../layout/tema';
import TelaInicial from './controles/telaInicial'
import { connect } from 'react-redux'
import Login from './controles/login'
import { doInitApp } from '../actions/aplicacao';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import Aplicacao from './controles/aplicacao';

const useStyles = makeStyles(theme => ({
    root: {
        height: '100vh',
    },

}));

const App = ({ autenticacao, parametros, onInitApp }) => {
    const classes = useStyles();
    useEffect(() => {
        onInitApp();
    }, [onInitApp])

 
    const mostraTelaInicial = !autenticacao || !parametros;
    const mostraAplicacao = autenticacao && autenticacao.autlogado && autenticacao.autlogado === true;

    const mostraLogin = !mostraTelaInicial && !mostraAplicacao;

    return (

        <Grid container component="main" className={classes.root}>
            <CssBaseline />
            <ThemeProvider theme={theme}>
                <Fragment>
                    {
                        mostraTelaInicial &&
                        <TelaInicial />
                    }
                    {
                        mostraLogin &&
                        <Login />
                    }
                    {
                        mostraAplicacao &&
                        <Aplicacao />
                    }
                </Fragment>
            </ThemeProvider>

        </Grid>

    );
}

const mapStateToProps = state => (
    {
        autenticacao: state.autenticacao,
        parametros: state.parametros
    }
)



const mapDispatchToProps = dispatch => ({
    onInitApp: () => dispatch(doInitApp()),
})


export default connect(mapStateToProps, mapDispatchToProps)(App);
