import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import { connect } from 'react-redux';
import { dbBuscaProdutos, dbBuscaProdutosFiltros } from '../../db/produto';
import { CssBaseline, Hidden } from '@material-ui/core';
import { Button } from '@material-ui/core';
import { FixedSizeList } from 'react-window';
import AutoSizer from 'react-virtualized-auto-sizer';
import { TextField } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container'
import Paper from '@material-ui/core/Grid';
const useStyles = makeStyles(theme => ({
    root: {
        width: '95vw',
        height: '80vh',
        paddingLeft:0,
        paddingRight:0,
        paddingTop: 6,
        backgroundColor: theme.palette.background.paper,
    },
    inline: {
        display: 'inline',
    },
    nomargin: {
        marginTop: 1,
        marginBottom: 0,
      },
}));

const ItensLista = ({ data, index, style }) => {
    const classes = useStyles();
    const produto = data[index];
    return (

        <Paper key={index} style={style} onClick={() => console.log(produto.cp1descr)}>
        <Grid container>
          <Grid item xs={2}>
          <Avatar alt={produto.cp1descr} src={`data:image/jpeg;base64,${produto.cp1imagem}`} />
          </Grid>
          <Grid item xs={10} container>
            <Grid item xs container direction="column" spacing={2}>
              <Grid item xs>
                <Typography gutterBottom noWrap variant="subtitle1">
                {`${produto.cp1descr}`}
                </Typography>
                <Typography variant="body2" gutterBottom>
                {`Ref.: ${produto.cp1ref99} Emb.: ${produto.cp1descr2} Est.: `}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                  {`Cód.: ${produto.cp1codig}`}
                </Typography>
              </Grid>
            </Grid>
            <Grid item>
              <Typography variant="subtitle1">$19.00</Typography>
            </Grid>
          </Grid>
        </Grid>
      </Paper>
    )
}

const ConsProdutos = () => {
    const classes = useStyles();

    const [produtos, setprodutos] = useState([]);

    const buscaProdutos = async () => {
        const prods = await dbBuscaProdutos();
        setprodutos(prods);
    }

    const handlePesquisa = async (e) => {
        const prods = await dbBuscaProdutosFiltros(e.target.value);
        setprodutos(prods);
    }

    return (
        <Container component="main" maxWidth="xs" className={classes.root}>
            <CssBaseline />
            <Grid container spacing={0}>
                    <TextField
                        id="outlined-dense"
                        label="código / nome / Referência"
                        margin="dense"
                        variant="outlined"
                        className={classes.nomargin}
                        fullWidth
                        onChange={(e)=>handlePesquisa(e)}
                    />

            <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                onClick={(e) => { e.preventDefault(); buscaProdutos() }}>
                Buscar</Button>
                </Grid>                
            {
                produtos.length > 0 &&
                <AutoSizer>
                    {({ height, width }) => (
                        <FixedSizeList height={height-30} width={width}  itemSize={120} itemCount={produtos.length} itemData={produtos}>
                            {ItensLista}
                        </FixedSizeList>
                    )}
                </AutoSizer>
            }
        </Container>
    );

}

const mapStateToProps = (state) => ({
    sincronizacao: state.sincronizacao,
})

export default connect(mapStateToProps)(ConsProdutos);