import { SIN_ADD } from "../constantes/actionsTypes";

const INITIAL_STATE = null

const applyAddSincronizacao = (state, action) => 
    action.sinc;



function parSincronizacao(state = INITIAL_STATE, action) {
    switch (action.type) {
        case SIN_ADD: {
            return applyAddSincronizacao(state, action);
        }
        default: return state;
    }
}

export default parSincronizacao;