import db from './dbvendas'




const dbLimpaSincProdutos = async(produtos) => {
    await db.produto.clear();
    await db.produto.bulkAdd(produtos);

}

const dbBuscaImagem = async(codigo) => {
    const produto = await db.produto.where('cp1codig').equals(codigo).first();
    return produto.cp1imagem;
}

const dbBuscaProdutos = async() => {
    const produtos = await db.produto.toCollection().toArray();
    return produtos;
}

const dbBuscaProdutosFiltros = async(busca) => {
    const termo = new RegExp(busca);
    const produtos = await db.produto.filter (function (prod) { 
        return termo.test(prod.cp1descr) || termo.test(prod.cp1codig) || termo.test(prod.cp1ref99); })
    .toArray();
    return produtos;
}

export {
    dbLimpaSincProdutos,
    dbBuscaImagem,
    dbBuscaProdutos,
    dbBuscaProdutosFiltros
}