import db from './dbvendas'

const dbAddIdentificacao = async(identificacao) => {
    await db.identificacao.put(identificacao);
}

const dbLoadIdentificacao = async() => {
    const identificacao = await db.identificacao.toCollection().first()
    return identificacao;
}

const dbAlterContacorrente = async(contacorrente) => {
    await db.identificacao.toCollection().modify({contacorrente: contacorrente});
}

export {
    dbAddIdentificacao,
    dbLoadIdentificacao,
    dbAlterContacorrente
}