import axios from 'axios';
import { API_BASE_URL } from '../constantes/apiConst';

const fetchAutenticacao = async login => {
    const response = await axios.get(
        `http://${login.autservidor}${API_BASE_URL}metodo=ola&usuario=${login.autusuario}&senha=${login.autsenha}`
    );

    return response.data;
};

export {
    fetchAutenticacao,
};