import React, { useEffect, useState } from 'react'
import Cartao from '../gerais/cartao';
import Dashboard from '../menu/dashboard';
import { connect } from 'react-redux';
import { BrowserRouter} from 'react-router-dom'


const Aplicacao = ({ sincronizacao }) => {
    const ultimaSincronizacao = sincronizacao.sincvalor;

    const [state, setstate] = useState([])

    useEffect(() => {

        function montaCartoes() {
            if (ultimaSincronizacao === 0) {
                const titulo = "Sincronização";
                const primario = "Primeiro acesso";
                const secundario = "Você ainda não fez nenhuma sincronização com o servidor.";
                const link = "Fazer sincronização";
                const cartao = <Cartao titulo={titulo}
                    principal={primario}
                    secundario={secundario}
                    link={link} />

                setstate(state => [...state, cartao])


            }
        }
        montaCartoes();
    }, [ultimaSincronizacao])

    return (
        <BrowserRouter>
            <Dashboard cartoes={state} />
        </ BrowserRouter>
    )
}

const mapStateToProps = (state) => ({
    sincronizacao: state.sincronizacao,
})



export default connect(mapStateToProps)(Aplicacao);
