import Dexie from 'dexie';


const db = new Dexie('vendas');
db.version(1).stores({
    cliente: `cl1codig, cl1nomec, cl1cpf, cl1cnpj, cl1munic`,
    produto: `cp1codig, cp1descr, cp1estacao, cp1posic`,
    estoque: `[estcodig+estfilial]`,
    promocao: `[procodig+prounida],proembalagem, procodbarra`,
    parametros: `parcodig`,
    autenticacao: `autid,autusuario`,
    identificacao: `vd1codig, vd1descr, filcodig`,
    parametrosinter: `parid`,
    sincronizacao: `sincpar, sincvalor`,
    titulosabertos: `[tp1numet+tp1ordem], tp1cdcli,tp1dtven, tp1dtemi`,
    notas: `[md1notaf+md1filial+md1codig+md1serie],md1devolucao,md1situa,md1data,md1tipod`,
    ultimos: `[md3notaf+md3filial+md3codig+md3serie+md3itens], md3descr, md3ref99`,

});


export default db;

