import React, { useState } from 'react'
import { makeStyles, createStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';
import Switch from '@material-ui/core/Switch';
import Button from '@material-ui/core/Button';
import { connect } from 'react-redux'
import { fetchAutenticacao } from '../../api/autenticacao';
import CircularProgress from '@material-ui/core/CircularProgress';
import CheckIcon from '@material-ui/icons/Check';
import Fab from '@material-ui/core/Fab';
import { green } from '@material-ui/core/colors';
import { fetchSinc } from '../../api/sincronizacao';
import { dbLimpaSincProdutos, dbBuscaImagem } from '../../db/produto';
import { dbLimpaSincClientes } from '../../db/cliente';
import { dbLimpaSincTitulos } from '../../db/titulosabertos';
import { dbLimpaSincEstoque } from '../../db/estoque';
import { dbLimpaSincPromocao } from '../../db/promocao';
import { Container } from '@material-ui/core';
import { dbLimpaSincNotas } from '../../db/notas';
import { dbLimpaSincUltimos } from '../../db/ultimos';
import { dbAlterContacorrente } from '../../db/identificacao';


const useStyles = makeStyles((theme) =>
    createStyles({
        root: {
            width: '100%',
            maxWidth: 360,
            backgroundColor: theme.palette.background.paper,
        },
        submit: {
            margin: theme.spacing(3, 0, 2),
        },
        success: {
            backgroundColor: green[500],
            '&:hover': {
                backgroundColor: green[700],
            },
        },
        iconSucces: {
            color: '#ffffff'
        },
    }),
);


function ItensSinc({ item, handleToggle, isChecked, getStatus }) {
    const classes = useStyles();
    return (
        <ListItem>
            <ListItemText id={`switch-list-label-${item}`} primary={item} />
            <ListItemSecondaryAction>
                {getStatus(item) === 'LOAD' && <CircularProgress />}
                {getStatus(item) === 'COMPLETO' &&
                    <Fab size='small' className={classes.success}>
                        <CheckIcon className={classes.iconSucces} />
                    </Fab>}
                <Switch
                    edge="end"
                    onChange={handleToggle(item)}
                    checked={isChecked(item)}
                    inputProps={{ 'aria-labelledby': `switch-list-label-${item}` }}
                />
            </ListItemSecondaryAction>
        </ListItem>
    )
}



function Sincronizacao({ autenticacao, identificacao }) {
    const classes = useStyles();
    const [checked, setChecked] = useState(['clientes', 'produtos', 'notas', 'planos']);
    const [load, setload] = useState([]);
    const [completo, setcompleto] = useState([]);
    const handleToggle = (value) => () => {

        const currentIndex = checked.indexOf(value);
        const newChecked = [...checked];

        if (currentIndex === -1) {
            newChecked.push(value);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        setChecked(newChecked);
    };

    const handleSincroniza = async () => {
        setload([]);
        setcompleto([]);
        for (const [idx, item] of checked.entries()) {
            setload((load) => [...load, item]);
            let todo;
            if (item === 'produtos') {
                todo = await fetchSinc(autenticacao, identificacao,'produtos');
                await dbLimpaSincProdutos(todo.data.produtos);
                await dbLimpaSincEstoque(todo.data.estoque);
                await dbLimpaSincPromocao(todo.data.promocao);
                
            } else if (item === 'clientes') {
                todo = await fetchSinc(autenticacao, identificacao,'clientes');
                await dbLimpaSincClientes(todo.data.clientes);
                await dbLimpaSincTitulos(todo.data.titulos);
            } else if (item === 'planos') {
                todo = await fetchSinc(autenticacao, identificacao,'planos');
            } else if (item === 'notas') {
                todo = await fetchSinc(autenticacao, identificacao,'notas');
                await dbLimpaSincNotas(todo.data.notas);
                await dbLimpaSincUltimos(todo.data.ultimos);
                if(todo.data.contacorrente && todo.data.contacorrente.length > 0){
                    await dbAlterContacorrente(todo.data.contacorrente);
                }
            }
            else {
                todo = await fetchAutenticacao(autenticacao);
            }
            setload([]);
            setcompleto((completo) => [...completo, item]);
            console.log(`Received Todo ${idx + 1}:`, todo);
        }

        console.log('Finished!');
    }

    const isChecked = (item) => checked.indexOf(item) !== -1

    const getStatus = (item) => {
        let status = '';
        if (load.indexOf(item) !== -1) {
            status = 'LOAD';
        }
        if (completo.indexOf(item) !== -1) {
            status = 'COMPLETO';
        }
        return status
    }

    return (
        <Container component="main" maxWidth="xs">
            <List subheader={<ListSubheader>Sincronização</ListSubheader>} className={classes.root}>
                <ItensSinc item='clientes' isChecked={isChecked} handleToggle={handleToggle} getStatus={getStatus} />
                <ItensSinc item='produtos' isChecked={isChecked} handleToggle={handleToggle} getStatus={getStatus} />
                <ItensSinc item='notas' isChecked={isChecked} handleToggle={handleToggle} getStatus={getStatus} />
                <ItensSinc item='planos' isChecked={isChecked} handleToggle={handleToggle} getStatus={getStatus} />
            </List>
            <Button
                fullWidth
                variant="contained"
                color="primary"
                disabled={false}
                className={classes.submit}
                onClick={(e) => { e.preventDefault(); handleSincroniza() }}>
                Sincronizar</Button>
        </Container>
    )
}

const mapStateToProps = (state) => ({
    autenticacao: state.autenticacao,
    identificacao: state.identificacao,
});

export default connect(mapStateToProps)(Sincronizacao);

