import db from './dbvendas'

const dbAddAutenticacao = async (autenticacao) => {
    await db.autenticacao.put({ ...autenticacao, ...{ autid: 1, autsenha: ''} });
}

const dbLoadAutenticacao = async () => {
    const autenticacao = await db.autenticacao.get(1)
    return autenticacao;
}

export {
    dbAddAutenticacao,
    dbLoadAutenticacao
}