import { AUT_LOGIN, AUT_ADD } from '../constantes/actionsTypes';
const INITIAL_STATE = null;

const autLogin = (state, action) =>
    ({ ...state, ...action.login })

const applyAddAutenticacao = (state, action) => {
    if (!action.aut) {
        return ({
            autusuario: '',
            autsenha: '',
            autservidor: '',
            autlogado: false
        });
    }
    return ({ ...state, ...action.aut })
}
function autReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case AUT_LOGIN: {
            return autLogin(state, action);
        }
        case AUT_ADD:
            return applyAddAutenticacao(state, action);
        default: return state;
    }
}

export default autReducer;