import { call, put } from "@redux-saga/core/effects";
import { doAddParam } from '../actions/parametros';
import { fetchAutenticacao } from "../api/autenticacao";
import { dbAddParametros } from "../db/parametros";
import { doFetchErrorNav } from "../actions/navegacao";
import { doAddAut } from '../actions/autenticacao';
import { dbAddAutenticacao } from '../db/autenticacao';
import { doAddIdent } from "../actions/identificacao";
import { dbAddIdentificacao } from "../db/identificacao";


function* handleFetchAutenticacao(action) {
    try {
        const { login } = action;
        const result = yield call(fetchAutenticacao, login);
        if(result.erro !== ''){
            throw result.erro;
        }
        yield put(doAddParam(result.data.parametros));
        yield call(dbAddParametros, result.data.parametros);
        yield put(doAddAut({...login,autlogado:true}));
        yield call(dbAddAutenticacao, login);
        yield put(doAddIdent(result.data.identificacao));
        yield call(dbAddIdentificacao, result.data.identificacao);
    } catch(error){
        const erro = error.message ? error.message : error;
        yield put(doFetchErrorNav(erro));
    }
}

export {
    handleFetchAutenticacao,
}