import { NAV_ERROR } from '../constantes/actionsTypes';
const INITIAL_STATE =
{
        loading: false,
        isError: false,
        erro: '',
        mensagem: '',
        alerta: '',
}

const applyFetchErrorNav = (state, action) =>(
    ({...state, ...{erro: action.error, isError: true}})
)

function navReducer(state= INITIAL_STATE, action){
    switch(action.type){
        case NAV_ERROR: {
            return applyFetchErrorNav(state, action);
        }
        default: return state;
    }
}

export default navReducer;