import db from './dbvendas'

const dbAddParametros = async(parametros) => {
    await db.parametros.put({...parametros, ...{parcodig:1}});
}

const dbLoadParametros = async() => {
    const parametros = await db.parametros.get(1)
    return parametros;
}

export {
    dbAddParametros,
    dbLoadParametros
}