import {combineReducers} from 'redux';
import autReducer from './autReducer';
import identReducer from './identReducer';
import navReducer from './navReducer';
import parInterReducer from './parInterReducer';
import parReducer from './parReducer';
import parSincronizacao from './sincReducer';

const rootReducers = combineReducers({
    autenticacao: autReducer,
    identificacao: identReducer,
    navegacao: navReducer,
    parametrosinter: parInterReducer,
    parametros: parReducer,
    sincronizacao: parSincronizacao,

})

export default rootReducers;