import { takeEvery, all } from "@redux-saga/core/effects";
import { AUT_FETCH, APP_INIT } from '../constantes/actionsTypes';
import { handleFetchAutenticacao } from "./autenticacao";
import { handleInitAplicacao } from "./aplicacao";


function *watchAll(){
    yield all([
        takeEvery(AUT_FETCH, handleFetchAutenticacao),
        takeEvery(APP_INIT,handleInitAplicacao)
    ])
}

export default watchAll;