import { call, put } from "@redux-saga/core/effects";
import { dbLoadParametros } from "../db/parametros";
import { doFetchErrorNav } from "../actions/navegacao";
import { doAddParam } from '../actions/parametros';
import { doAddAut } from '../actions/autenticacao';
import { dbLoadAutenticacao } from '../db/autenticacao';
import { dbLoadAddSincronizacao } from "../db/sincronizacao";
import { doAddSin } from "../actions/sincronizacao";
import { dbLoadIdentificacao } from "../db/identificacao";
import { doAddIdent } from "../actions/identificacao";



function* handleInitAplicacao() {
    try {
        const sincronizacao = yield call(dbLoadAddSincronizacao);
        yield put(doAddSin(sincronizacao));
        const parametros = yield call(dbLoadParametros);
        yield put(doAddParam(parametros));
        const autenticacao = yield call(dbLoadAutenticacao);
        yield put(doAddAut(autenticacao));
        const identificacao = yield call(dbLoadIdentificacao);
        yield put(doAddIdent(identificacao));
    } catch (error) {
        const erro = error.message ? error.message : error;
        yield put(doFetchErrorNav(erro));
    }
}

export {
    handleInitAplicacao,
}