import { IDE_ADD } from "../constantes/actionsTypes";

const INITIAL_STATE = null;

const applyAddIdentificacao = (state, action) => {
    if (!action.identificacao) {
        return {};
    }
    return action.identificacao;
}


function identReducer(state= INITIAL_STATE, action){
    switch(action.type){
        case IDE_ADD: {
            return applyAddIdentificacao(state, action);
        }
        default: return state;
    }
}

export default identReducer;







